<!--  -->
<template>
    <div class='listMore'>
        <img :src="img" alt="" class='bgimg' >
        <div class='moreBox'>
            <div class='morelist' v-for='(item,index) in list' :key='index'  @click="detailsName(item.productId)" >
                <img :src="item.picture" alt="" class='pro-img'>
                <p class='pro-title'>{{item.title}}</p>
                <div class='money'>
                    <span class='money1'>￥{{item.sellPrice}}</span>
                    <span class='money2' v-if='item.marketPrice!=null' >￥{{item.marketPrice}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    //这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
    //例如：import 《组件名称》 from '《组件路径》';
    import {listMore} from '../../api/api'
    import { mapActions } from 'vuex'
    export default {
    //import引入的组件需要注入到对象中才能使用
    components: {},
    data() {
    //这里存放数据
    return {
        list:[],
        img:'',
        color:''
    };
    },
    //监听属性 类似于data概念
    computed: {},
    //监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        ...mapActions(['headTitle','showBtm','goBack']),
         // 跳转详情页
        detailsName:function(id){
            this.$router.push('/shopDetails/'+id)
        },
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {
        this.headTitle('专题活动')
        this.showBtm(false)
        this.goBack(true)
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
        this.loading = this.$toast.loading();
        listMore(this.$route.params.id).then(response=>{
            this.loading.hide();
            this.img=response.data.data.img
            this.color=response.data.data.color
            this.list=response.data.data.productList
        });
    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
    }
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
    .listMore{
        margin-top:90px;
        background:#fff;
    }
    .bgimg{
        width:100%;
    }
    .moreBox{
        width:100%;
        overflow: hidden;
        display: flex;
        flex-wrap: wrap;
    }
    .morelist{
        width:218px;
        border:6px solid #fff;
        margin:0 16px;
        margin-bottom:15px;
    }
    .pro-img{
        width:218px;
        height:218px;
        display:block;
    }
    .pro-title{
        width:100%;
        white-space:nowrap;
        text-overflow:ellipsis;
        font-size:22px;
        color:#000;
        margin-bottom:15px;
        margin-top:10px;
        overflow: hidden;
    }
    .pro-money{
        display:flex;
        align-items:center;
    }
    .money1{
        font-size:30px; 
        color:rgb(255,0,0); 
        margin-right:15px; 
    }
    .money2{
        font-size:22px; 
        color:rgb(159,159,159); 
        text-decoration: line-through; 
    }
</style>


